.textarea {
  width: 100%;
  height: 22px;
  font-size: 18px;
  font-weight: 500;
  color: rgb(172, 172, 172);
  outline: none;
  border: none;
  border: none;
  resize: none;
  outline: none;
  place-content: none;
  text-align: left;
}

.textarea::placeholder{
  text-align: center;
}

.textarea:focus {
  outline: solid;
  outline-width: 2px;
  outline-color: rgb(32, 90, 251);
}